import React from 'react'
import './App.css'
import Note from './Note'

const createReactClass = require('create-react-class');

const Board = createReactClass({
    propTypes: {
        count: function(props, propName) {
            if (typeof props[propName] !== "number") {
                return new Error("board count must be a number")
            }
            if (props[propName] > 100) {
                return new Error("currently can only create 100 notes")
            }
        }
    },
    getInitialState() {
        if (this.props.count) {
            let url = `https://baconipsum.com/api/?type=all-meat&sentences=${this.props.count}`
            fetch(url)
                .then(results => results.json())
                .then(array   => array[0])
                .then(text    => text.split(". "))
                .then(array   => array.forEach(
                     sentence => this.add(sentence)))
                .catch(function(err) {
                    console.log("Couldn't connect to the Bacon API", err)
                })
        }
        return {
            notes: []
        }
    },
    nextId() {
        this.uniqueId = this.uniqueId || 0
        return this.uniqueId++
    },
    add(text) {
        const notes = [
            ...this.state.notes,
            {
                id: this.nextId(),
                note: text
            }
        ] 
        this.setState({notes})
    },
    update(newText, id) {
        const notes = this.state.notes.map(
            note => (note.id !== id) ?
                note :
                {
                    ...note,
                    note: newText
                }
        )
        this.setState({notes})
    },
    remove(id) {
        const notes = this.state.notes.filter(
            note => (note.id !== id)
        )
        this.setState({notes})
    },
    eachNote(note) {
        return (<Note key={note.id}
                       id={note.id}
                       onChange={this.update}
                       onRemove={this.remove}>
                    {note.note}
                </Note>)
    },
    render() {
        return (<div className="board">
                    {this.state.notes.map(this.eachNote)}
                    <button onClick={() => this.add("New Note")}>+</button>
                </div>
        )
    }
})

export default Board
