import React from 'react'
import './App.css'
import Draggable from 'react-draggable'

const createReactClass = require('create-react-class');

const Note = createReactClass({
    getInitialState() {
        // Note that a "note" is 150 pixels by 150 pixels
        //     as defined in "style.css"
        this.style = {
            right: this.randomBetween(0, window.innerWidth  - 150, "px"),
            top:   this.randomBetween(0, window.innerHeight - 150, "px")
        }
        return {editing: false}
    },
    componentDidUpdate() {
        if (this.state.editing) {
            this.refs.newText.focus()
            this.refs.newText.select()
        }
    },
    shouldComponentUpdate(nextProps, nextState) {
        // Performance optimization: do not re-render if state unchanged
        return this.props.children !== nextProps.children ||
               this.state          !== nextState
    },
    randomBetween(x, y, units) {
        return (x + Math.ceil(Math.random() * (y - x))) + units
    },
    edit() {
        this.setState({editing: true})
    },
    save() {
        this.props.onChange(this.refs.newText.value, this.props.id)
        this.setState({editing: false})
    },
    remove() {
        this.props.onRemove(this.props.id)
    },
    renderForm() {
        return (
            <div className="note" style={this.style}>
                <textarea ref="newText" defaultValue={this.props.children}>
                </textarea>
                <button onClick={this.save}>SAVE</button>
            </div>
        )
    },
    renderDisplay() {
        return (
            <div className="note" style={this.style}>
                <p>{this.props.children}</p>
                <span>
                    <button onClick={this.edit}>EDIT</button>
                    <button onClick={this.remove}>X</button>
                </span>
            </div>
        )
    },
    render() {
        return (<Draggable>
            {(this.state.editing) ? this.renderForm() : this.renderDisplay()}
        </Draggable>)
    }
})

export default Note
